@import "../../global.scss";

.nextButton {
  position: absolute;
  bottom: 45px;
  left: 50%;

  @include mobile {
    left: calc(50% - 20px);
  }

  img {
    width: 40px;
    animation: arowBlink 2s infinite ease-in-out;
  }

  @keyframes arowBlink {
    100% {
      opacity: 0;
    }
  }
}
