@import "../../global.scss";

.intro {
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    overflow: hidden;

    .imgContainer {
      width: 700px;
      height: 700px;
      background-color: #dc143c;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      float: right;

      @include mobile {
        width: 550px;
        height: 550px;
        align-items: flex-start;
      }

      img {
        height: 90%;
        @include mobile {
          height: 75%;
        }
      }
    }
  }

  .right {
    flex: 1;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile {
        padding-left: 0;
        align-items: center;
      }

      h1 {
        font-size: 75px;
        font-weight: 700;
        margin: 10px 0;

        @include mobile {
          font-size: 45px;
        }
      }

      h2 {
        font-size: 40px;
        font-weight: 500;

        @include mobile {
          font-size: 30px;
        }
      }

      h3 {
        font-size: 45px;
        display: flex;
        gap: 10px;

        @include mobile {
          font-size: 30px;
        }

        span {
          color: #dc143c;
        }
      }
    }
  }
}
