@import "../../global.scss";

.portfolioList {
  font-size: 14px;
  padding: 7px 14px;
  border-radius: 6px;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 50px;

    @include mobile {
      margin-right: 10px;
    }
  }

  &.active {
    background-color: $mainColor;
    color: #fafafa;
  }
}
