@import "../../global.scss";

.sliderContainer {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;

  @include mobile {
    &:nth-child(2) {
      margin: 15px 0;
    }
  }

  .item {
    width: 700px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fafafa;
    border-radius: 20px;

    @include mobile {
      width: 95%;
      height: 175px;
    }

    .left {
      flex: 4;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      @include mobile {
        flex: 5;
      }

      .leftContainer {
        width: 90%;
        height: 70%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @include mobile {
          height: 80%;
        }

        .imgContainer {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background-color: #dd9378;
          display: flex;
          align-items: center;
          justify-content: center;

          img {
            width: 25px;
            height: 25px;
          }
        }

        h2 {
          font-size: 20px;

          @include mobile {
            font-size: 16px;
          }
        }

        p {
          font-size: 14px;

          @include mobile {
            font-size: 12px;
          }
        }

        span {
          font-size: 12px;
          font-weight: 600;
          text-decoration: underline;
          cursor: pointer;
        }
      }
    }

    .right {
      flex: 8;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;

      @include mobile {
        flex: 7;
      }

      img {
        width: 400px;
        transform: rotate(-10deg);
      }
    }
  }
}
