@import "../../global.scss";

.testimonials {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  h1 {
    font-size: 50px;
    margin: 20px 0;

    @include mobile {
      font-size: 30px;
    }
  }

  .container {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
      flex-direction: column;
    }

    .card {
      width: 250px;
      height: 70%;
      border-radius: 10px;
      box-shadow: 0 2px 15px -8px #0c0c0ca5;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 20px;
      transition: all 1s ease;

      @include mobile {
        width: 290px;
        height: 180px;
      }

      &.featured {
        width: 300px;
        height: 75%;
        margin: 0 30px;

        @include mobile {
          width: 290px;
          height: 180px;
          margin: 8px 0;
        }
      }

      &:hover {
        transform: scale(1.05);
      }

      .top {
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          &.left,
          &.right {
            height: 25px;

            @include mobile {
              height: 15px;
            }
          }

          &.user {
            height: 90px;
            width: 90px;
            border-radius: 50%;
            margin: 0 20px;
            object-fit: cover;

            @include mobile {
              height: 40px;
              width: 40px;
            }
          }
        }
      }

      .center {
        padding: 20px 12px;
        border-radius: 10px;
        background-color: #fdf2f5;
        font-size: 13px;
        line-height: 1.5;

        @include mobile {
          padding: 10px 8px;
          font-size: 10px;
        }
      }

      .bottom {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        h3 {
          font-size: 20px;
          margin-bottom: 5px;

          @include mobile {
            font-size: 12px;
          }
        }

        h4 {
          font-size: 15px;
          color: #cacaca;

          @include mobile {
            font-size: 9px;
          }
        }
      }
    }
  }
}
