@import "../../global.scss";

.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  h1 {
    font-size: 50px;
    font-weight: 700;
    margin: 15px 0;

    @include mobile {
      font-size: 30px;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 16px;

    @include mobile {
      width: 100%;
    }

    .item {
      width: 280px;
      height: 200px;
      border-radius: 20px;
      border: solid 1px #fafafa;
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;

      @include mobile {
        width: 130px;
        height: 110px;
      }

      h3 {
        font-size: 20px;
        color: #fafafa;
        position: absolute;
        text-align: center;

        @include mobile {
          font-size: 18px;
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;

        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
