@import "../../global.scss";

.works {
  background-color: #dc143c;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;

    @include mobile {
      display: none;
    }

    &.left {
      left: 50px;
      transform: rotate(180deg);
    }

    &.right {
      right: 50px;
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height: calc(100vh - 70px);
      flex-direction: column;
      justify-content: center;
    }
  }
}
