@import "../../global.scss";

.contact {
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }

  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
      font-size: 30px;

      @include mobile {
        margin-bottom: 16px;
      }
    }

    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      gap: 8px;

      input {
        width: 300px;
        height: 30px;
        font-size: 14px;
        padding: 8px;

        @include mobile {
          width: 250px;
        }
      }

      textarea {
        width: 300px;
        height: 250px;
        font-size: 14px;
        padding: 8px;

        @include mobile {
          width: 250px;
          height: 100px;
          font-size: 12px;
        }
      }

      button {
        width: 300px;
        height: 40px;
        color: #fafafa;
        background-color: #dc143c;
        border: none;
        font-weight: 500;
        border-radius: 10px;
        padding: 8px 16px;
        cursor: pointer;
      }

      span {
        color: #dc143c;
        font-size: 12px;
        padding: 8px;

        @include mobile {
          font-size: 10px;
        }
      }
    }
  }
}
